import { SingletonManager } from './singleton-manager'

let singletonKeys = {}

/**
 * Add new singleton
 * @param {string} key
 * @param {SingletonManager} singletonManager
 */
export const addSingleton = (
  key: string,
  singletonManager: SingletonManager
) => {
  singletonKeys = {
    ...singletonKeys,
    [key]: singletonManager,
  }
}

/**
 * Get singleton
 * @param {string} key
 */
export const getSingleton = (key: string) =>
  singletonKeys[key] as SingletonManager
