import { useContext } from 'react'
import { SingletonContext } from './singleton-provider'
import { getSingletonManager } from './get-singleton-manager'

/**
 * Use Singleton hook. Will bring back the desired Singleton created previously using
 * createSingleton hook.
 *
 * You will be provided with the following props:
 * - singleton (The singleton you've created itself);
 * - singletonStatus (The status of singleton. Will return 'in_progress' if there are any process
 * in progress, e.g: promises, await, fetch, etc. Will return 'ready' when all the process
 * are done and all internal singleton's props are set);
 * - singletonStatusObservable (This is basically an observable that will notify the status data for its subscribers. You
 * may want use this in some custom service/hook)
 * - managerStatus: (The status of the global singleton manager. If you have more than one Singleton, this
 * is the prop that will handle the global status being affected for all Singletons)
 *
 * @param singletonManager
 */
export function useSingleton<C>(singletonManager: C) {
  const { status: singletonManagerStatus } = useContext(SingletonContext)
  const { singleton, status, observable } = getSingletonManager(
    singletonManager
  )

  return {
    singleton,
    singletonStatus: status,
    singletonStatusObservable: observable,
    managerStatus: singletonManagerStatus,
  }
}
