import React, { createContext, useState, useEffect } from 'react'
import { Status } from './types'
import { getObservablesKeys, getObservable } from './observable-keys'

interface SingletonContextProps {
  // Singleton's provider status (global)
  status: Status
}

const defaultProps: SingletonContextProps = {
  status: 'ready',
}

export const SingletonContext = createContext<SingletonContextProps>(
  defaultProps
)

interface SingletonProviderProps {
  readonly children: React.ReactNode
}

export const SingletonProvider = ({ children }: SingletonProviderProps) => {
  const [status, setStatus] = useState<Status>(defaultProps.status)
  const observablesKeys = getObservablesKeys()

  // Watch all the singletons status. This is used to turn every singleton reactive
  useEffect(() => {
    observablesKeys.forEach((observableKey) => {
      const currentObservable = getObservable(observableKey)
      currentObservable.subscribe((statusData) => {
        setStatus(statusData)
      })
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <SingletonContext.Provider value={{ status }}>
      {children}
    </SingletonContext.Provider>
  )
}
