import Observable from './observable'
import { Singleton, Status } from './types'
import { addObservable, getObservable } from './observable-keys'
import uniqueId from './unique-id'
import { addSingleton } from './singleton-keys'

type UseWatcher = (done: () => void) => void

// Internal Class to control the Singletons classes
export class SingletonManager {
  private singletonManager: any

  public status: Status = 'ready'

  public key = uniqueId()

  constructor() {
    // Create its observable status
    addObservable(this.key, new Observable<Status>())
  }

  public setValue<C extends Singleton>(singletonManager: C) {
    const ref = singletonManager as Singleton
    const properties = Object.getOwnPropertyNames(ref)
    if (properties.indexOf('getInstance') < 0) {
      throw new Error(
        'You must create a public static method called getInstance for initiate the Singleton.'
      )
    }

    if (properties.indexOf('instance') < 0) {
      throw new Error(
        'You must create a public static prop called instance that will be used to store the singleton instance itself.'
      )
    }

    this.singletonManager = ref
  }

  public getProps() {
    return {
      singleton: this.singletonManager,
      status: this.status,
      key: this.key,
    }
  }
}

//
export function createSingleton() {
  const newSingleton = new SingletonManager()

  return {
    /**
     * Method/Function that should be reactive. When the desired changes are done, you
     * should call done method, so that, React will know that a change happened
     */
    useWatcher: (method: UseWatcher) => {
      const observable = getObservable(newSingleton.key)
      newSingleton.status = 'in_progress'
      observable.notify(newSingleton.status)
      method(() => {
        newSingleton.status = 'ready'
        observable.notify(newSingleton.status)
      })
    },

    /**
     * Set the Singleton Class / Object
     */
    setValue<C extends Singleton>(classValue: C) {
      newSingleton.setValue(classValue)
      addSingleton((classValue as any).name, newSingleton)
    },
  }
}
